<script>
  import { onMount } from 'svelte';
  import dayjs from "dayjs";
  import PostContent from "../components/PostContent.svelte";

  export let slug;

  const api = window.ghostAPI;
  let data = {};
  
  onMount(async () => {
		data = await api.posts.read({ slug });
	});
</script>

